import React from "react";

function Detailes({ songs }) {
  return (
    <div className="p-detailes">
      <div className="detaile-img">
        <img src={songs.img_src} alt="Song Poster" />
      </div>
      <div className="detaile-title">{songs.title}</div>
      <div className="detaile-artist">By: {songs.artist}</div>
    </div>
  );
}

export default Detailes;
