import { useState, useEffect, useRef } from "react";
import Detales from "./Detailes";
import Controls from "./Controls";

function Player({
  songs,
  currentSongIndex,
  setCurrentSongIndex,
  nextSongIndex,
}) {
  const audioEl = useRef(null);
  const [isPlaying, setIsPlaying] = useState(false);

  useEffect(() => {
    if (isPlaying) {
      audioEl.current.play();
    } else {
      audioEl.current.pause();
    }
  });

  // Skip Song Function
  const skipSong = (forwards = true) => {
    if (forwards) {
      setCurrentSongIndex(() => {
        let temp = currentSongIndex;
        temp++;

        if (temp > songs.length - 1) {
          temp = 0;
        }

        return temp;
      });
    } else {
      setCurrentSongIndex(() => {
        let temp = currentSongIndex;
        temp--;

        if (temp < 0) {
          temp = songs.length - 1;
        }

        return temp;
      });
    }
  };

  return (
    <div className="p-home">
      <audio src={songs[currentSongIndex].src} ref={audioEl}></audio>
      <h2>Playing Now</h2>
      <Detales songs={songs[currentSongIndex]} />
      <Controls
        isPlaying={isPlaying}
        setIsPlaying={setIsPlaying}
        skipSong={skipSong}
      />
      <p>
        <strong>Next Song:</strong> {songs[nextSongIndex].title} By{" "}
        {songs[nextSongIndex].artist}
      </p>
    </div>
  );
}

export default Player;
